        .apply-version {
            .el-dialog__body {
                /*padding: 0;*/
            }
            p {
                padding-bottom: 10px;
            }
            section {
                display: flex;
                margin-bottom: 10px;
                >i {
                    display: block;
                    width: 100px;
                    text-align: right;
                }
                .el-checkbox-group {
                    width: 80%;
                    .el-checkbox {
                        margin-left: 15px;
                        margin-bottom: 10px;
                    }
                }
            }
        }
        .block {
            text-align: right;
