                </el-form-item>
                <!--<el-form-item prop="used" label="">
                    <el-radio-group v-model="fetchGroup.used">
                        <el-radio :label="0"></el-radio>
                        <el-radio :label="1"></el-radio>
                    </el-radio-group>
                </el-form-item>-->
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addForm = false"> </el-button>
